
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin-top: 60px;
}

#table-footer{
    padding-top: 2rem;
}
.page-number{
    padding: 1rem;
    margin: .3rem;
    background-color: #007bff;
    color: #fff;
    border-radius: .3rem;
    cursor: pointer;
}
table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}
td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}
th{
    cursor: pointer;
}
label {
   color: #007bff; 
   margin: 1.5em 0 1em;
   font-size: 22px;
   text-align: center;
}
input{
    width: 25%;
    height: 120%;
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    display: flex;
}


